const {rename_cls} = require("nv-facutil-name");


function creat_event_cls(name='E') {
    class _E extends Event {
        #data
        constructor(typ,data) {
            super(typ);
            this.#data = data
        }
        get data() {return(this.#data)}
        set data(v) {this.#data = v}
    }
    rename_cls(_E,name);
    return(_E)
}

const E = creat_event_cls();


function creat_event_target_cls(name='ET') {
    class _ET extends EventTarget {
        #mp = new Map()
        addEventListener(typ,handler) {
            super.addEventListener(typ,handler);
            this.#mp.set(typ,handler)
        }
        ls() {return(this.#mp)}
        removeEventListener(typ) {
            super.removeEventListener(typ);
            this.#mp.delete(typ)
        }
        dispatch(typ,data) {
            let e = new E(typ,data);
            this.dispatchEvent(e);
            return(e);
        }
    }
    rename_cls(_ET,name);
    return(_ET)
}


const ET = creat_event_target_cls()


module.exports = {
    creat_event_cls,
    E,
    creat_event_target_cls,
    ET,
}
